import React, { useEffect, useState } from 'react'
import { fetchPoolKeys, getTokenAccountsByOwner } from '../../../utils/raydium/devnet'
import { Connection, PublicKey } from '@solana/web3.js'
import { Liquidity, Percent, Token, TokenAmount } from '@raydium-io/raydium-sdk'
import { useWallet } from '@solana/wallet-adapter-react'
import SwapDetail from './SwapDetail'
import BigNumber from 'bignumber.js'

//BONK_SOL
const POOL_ID = 'GGj7YKTJdavHv2F7WcCic2SqEdPcZK1EWFfGDZMbDLo4'

//SOL_USDT
// const POOL_ID = '384zMi9MbUKVUfkUdrnuMfWBwJR9gadSxYimuXeJ9DaJ'

// const connection = new Connection('https://metaplex.devnet.rpcpool.com/', 'confirmed')
const connection = new Connection(
  'https://hardworking-white-waterfall.solana-mainnet.quiknode.pro/28532ff7951782356fb7fdc0c3213407035ca202/',
  'confirmed',
)

const useSwap = ({ chainId, toCurrency, solAmountIn, swapEnabled }) => {
  const [tokenAccounts, setTokenAccounts] = useState([])
  const [poolKeys, setPoolKeys] = useState({})
  const [poolInfo, setPoolInfo] = useState({})
  const [quote, setQuote] = useState({})
  const [isLoading, setIsLoading] = useState(false)

  const { publicKey } = useWallet()

  useEffect(() => {
    if (toCurrency !== 'default' && chainId && Number(chainId) === 9090 && swapEnabled) {
      ;(async () => {
        try {
          setIsLoading(true)
          const amountAndBuffer = new BigNumber(1)
          const _toSolQuote = await getQuote(amountAndBuffer, true)
          setQuote(_toSolQuote)
        } catch (e) {
          console.log(e)
        } finally {
          setIsLoading(false)
        }
      })()
    }
  }, [toCurrency, solAmountIn])

  const getQuote = async (baseAmount, swapCurrency = false) => {
    debugger
    let TA = tokenAccounts
    let PK = poolKeys
    let PI = poolInfo
    if (!TA.length || !PK || !PI) {
      TA = await getTokenAccountsByOwner(connection, publicKey)
      setTokenAccounts(TA)
      PK = await fetchPoolKeys(connection, new PublicKey(POOL_ID))
      setPoolKeys(PK)
      PI = await Liquidity.fetchInfo({ connection, poolKeys: PK })
      setPoolInfo(PI)
    }

    let currencyInMint = PK.baseMint
    let currencyInDecimals = PI.baseDecimals
    let currencyOutMint = PK.quoteMint
    let currencyOutDecimals = PI.quoteDecimals

    if (swapCurrency) {
      currencyInMint = PK.quoteMint
      currencyInDecimals = PI.quoteDecimals
      currencyOutMint = PK.baseMint
      currencyOutDecimals = PI.baseDecimals
    }

    const amountIn = new TokenAmount(new Token(currencyInMint, currencyInDecimals), baseAmount.toFixed(), false)

    const currencyOut = new Token(currencyOutMint, currencyOutDecimals)

    // 5% slippage
    const slippage = new Percent(5, 100)

    const _quote = Liquidity.computeAmountOut({
      poolKeys: PK,
      poolInfo: PI,
      amountIn,
      currencyOut,
      slippage,
    })

    const { amountOut, minAmountOut, currentPrice, executionPrice, priceImpact, fee } = _quote

    console.log(
      `swap: ${PK.id.toBase58()}, amountIn: ${amountIn.toFixed()}, amountOut: ${amountOut.toFixed()}, executionPrice: ${executionPrice.toFixed()}, fee: ${fee.toFixed()}, priceImpact: ${priceImpact.toFixed()}`,
    )

    return {
      amountIn,
      amountOut,
      poolKeys: PK,
      tokenAccounts: TA,
      ..._quote,
    }
  }

  const getSwapTx = async (amount) => {
    // const { amountOut: amountNeededToMint } = await getQuote(amount.toFixed(), true)
    const { amountIn, amountOut, poolKeys, tokenAccounts } = await getQuote(amount, false)
    const { transaction, signers } = await Liquidity.makeSwapTransaction({
      connection,
      poolKeys,
      userKeys: {
        tokenAccounts,
        owner: publicKey,
      },
      amountIn,
      amountOut,
      fixedSide: 'in',
    })

    return {
      transaction,
      signers,
    }
  }

  const renderSwapDetails = () => {
    return <SwapDetail quote={quote} getQuote={getQuote} toCurrency={toCurrency} solAmountIn={solAmountIn} />
  }

  // const requiredCustomAmount = quote?.amountOut?.toFixed() ?? 0

  const solAmountInBn = new BigNumber(solAmountIn)
  // const requiredCustomAmount = new BigNumber(solAmountIn).dividedBy(quote.minAmountOut.toFixed() ?? 1).toFixed() ?? 0
  const requiredCustomAmount = quote.minAmountOut
    ? solAmountInBn.dividedBy(quote.minAmountOut.toFixed()).toFixed(4)
    : solAmountInBn.toFixed()

  return { getQuote, getSwapTx, connection, renderSwapDetails, requiredCustomAmount, isLoading }
}

export default useSwap
