import React from 'react'
import useUserAgent from '../../utils/userAgent'
import { Box, Text } from '@chakra-ui/react'
import Option from './Option'
import STACKS_ICON_URL from '../Svgs/stacksWalletIcon.png'

const BASE_PROPS = {
  color: '#315CF5',
  icon: STACKS_ICON_URL,
  id: 'stacks-wallet',
}

function StacksWalletOption({ tryActivation }) {
  return <Option {...BASE_PROPS} isActive={false} onClick={() => tryActivation('stacks')} header={'Stacks'} />
}

const StacksWalletOptions = ({ tryActivation, showTitle = true }) => {
  const isMobile = useUserAgent()
  const getOptions = () => {
    return <StacksWalletOption tryActivation={tryActivation} />
  }
  return (
    <Box bg="background">
      {showTitle && (
        <Text color="foreground" fontSize={18} fontWeight={500}>
          Connect Wallet
        </Text>
      )}
      {getOptions()}
    </Box>
  )
}

export default StacksWalletOptions
