import { Box, Flex, HStack, Image, Link, Tag, TagCloseButton, TagLabel, Text } from '@chakra-ui/react'
import { useWeb3React } from '@web3-react/core'
import React, { lazy, useEffect, useState } from 'react'
import WelcomeScreen from './WelcomeScreen'
import WalletModal from '../../components/WalletModal'
import SolanaWalletModal from '../../components/WalletModal/SolanaWalletModal'
import Widget from './Widget'
import Loader from '../../components/Loader'
import { updateSelectedWallet } from '../../state/user/reducer'
import { truncateAddress } from '../../utils'
import useCollection from './Widget/hooks/useCollection'
import { useWallet } from '@solana/wallet-adapter-react'
import { useDispatch, useSelector } from 'react-redux'
import { BG, Centered, ModalFooter, StyledCloseIcon, StyledModal } from './styles'
import Providers from './Providers'
import { getClientDetails } from '../../state/client/source'
import square from '../../assets/images/bandit-B.png'
import AllWallets from '../../components/WalletModal/AllWallets'
import { SELECTABLE_WALLETS } from '../../hooks/useOrderedConnections'
import useStacks from '../../components/WalletModal/hooks/useStacks'
import StacksWalletOptions from '../../components/WalletModal/StacksWalletOptions'
import StacksWalletModalView from '../../components/WalletModal/StacksWalletModal'

const MintView = (props) => {
  const [clientConfig, setClientConfig] = useState({})
  const { accessKey } = props

  useEffect(() => {
    async function fetchConfig(accessKey) {
      try {
        const config = await getClientDetails(accessKey)
        setClientConfig(config)
      } catch (e) {
        console.log(e)
      }
    }
    if (accessKey) {
      fetchConfig(accessKey)
    }
  }, [accessKey])

  if (!props.mintOpen) {
    return null
  }
  return (
    <Providers clientConfig={clientConfig}>
      <Mint {...props} clientConfig={clientConfig} />{' '}
    </Providers>
  )
}

const Mint = ({ onClose, collectionId, accessKey, collection, clientConfig }) => {
  const selectedWallet = useSelector((state) => state.user.selectedWallet)
  const [showWallets, setShowWallets] = useState(false)
  const { account, connector } = useWeb3React()
  const { publicKey: solanaAccount, disconnect } = useWallet()
  const { collectionState, isLoading, cm, isFetching } = useCollection(collectionId, accessKey, collection)

  const dispatch = useDispatch()

  const { userSession, stacksAddress } = useStacks()

  const { chainId } = collectionState

  const onClickClose = () => {
    if (!!onClose) {
      onClose()
    }
  }

  const onClickConnectWallet = () => {
    setShowWallets(true)
  }
  const disconnectEvmWallet = () => {
    try {
      if (connector.deactivate) {
        connector.deactivate()
      } else {
        connector.resetState()
      }
      dispatch(updateSelectedWallet({ wallet: undefined }))
    } catch (e) {
      console.log(e)
    }
  }

  const getLoadingScreen = () => {
    return (
      <Flex justify="center" align="center" height="250px">
        <Loader />
      </Flex>
    )
  }

  const getContent = () => {
    const isStacks = [6060, 6061].includes(chainId)
    if (
      (selectedWallet === 'stacks' && stacksAddress) ||
      (selectedWallet === 'Phantom' && solanaAccount) ||
      (SELECTABLE_WALLETS.includes(selectedWallet) && account)
    ) {
      return (
        <Widget
          onClickClose={onClickClose}
          collectionState={collectionState}
          cm={cm}
          isCollectionFetching={isFetching}
          clientConfig={clientConfig}
        />
      )
    } else if (isStacks && showWallets) {
      return <StacksWalletModalView clientConfig={clientConfig} />
    } else if (showWallets) {
      return <AllWallets />
    } else {
      return <WelcomeScreen onClickConnectWallet={onClickConnectWallet} clientConfig={clientConfig} />
    }
  }

  const isSolana = [9090, 9091].includes(chainId)

  const solanaAccountStr = !!solanaAccount ? solanaAccount.toBase58() : ''

  return (
    <BG>
      <Centered>
        <StyledModal bg="background">
          {(selectedWallet === 'stacks'
            ? stacksAddress
            : selectedWallet === 'Phantom'
            ? solanaAccountStr
            : account) && (
            <HStack mb={3} justify="flex-start">
              <Tag size={'lg'} key={'lg'} borderRadius="full" variant="outline" colorScheme="gray">
                <TagLabel>
                  {selectedWallet === 'stacks'
                    ? truncateAddress(stacksAddress, 5)
                    : selectedWallet === 'Phantom'
                    ? truncateAddress(solanaAccountStr, 5)
                    : truncateAddress(account, 5)}
                </TagLabel>
                <TagCloseButton onClick={selectedWallet === 'Phantom' ? disconnect : disconnectEvmWallet} />
              </Tag>
            </HStack>
          )}
          <StyledCloseIcon onClick={onClickClose} />
          <Box bg="background" height="100%" width="100%" flex="1" pb={'30px'}>
            {isLoading && getLoadingScreen()}

            {!isLoading && getContent()}
          </Box>

          <ModalFooter bg="background">
            <Flex alignItems="center">
              <Image src={square} width={'15px'} height={'15px'} alt="" />
              <Link href={'https://bandit.network'} target="_blank">
                <Text fontSize={12} ml={1}>
                  Powered by bandit.network
                </Text>
              </Link>
            </Flex>
          </ModalFooter>
        </StyledModal>
      </Centered>
    </BG>
  )
}

export default MintView
