import { useWallet } from '@solana/wallet-adapter-react'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import PendingView from '../../components/WalletModal/PendingView'
import SolanaWalletOptions from '../../components/WalletModal/SolanaWalletOptions'
import { updateSolanaSelectedWallet } from '../../state/user/reducer'
import { Modal, ModalBody, ModalCloseButton, ModalContent, ModalOverlay, Portal } from '@chakra-ui/react'

const WALLET_VIEWS = {
  OPTIONS: 'options',
  ACCOUNT: 'account',
  PENDING: 'pending',
}

export const SolanaWalletModal = ({ isOpen, onClose }) => {
  return (
    <Modal isOpen={isOpen} onClose={onClose} isCentered size="xs" portalProps={'containerRef'}>
      <ModalOverlay />
      <ModalContent>
        <ModalCloseButton />
        <ModalBody>
          <SolanaWalletView />
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}

const SolanaWalletView = () => {
  const [walletView, setWalletView] = useState(WALLET_VIEWS.OPTIONS)

  const walletAdapter = useWallet()

  const dispatch = useDispatch()

  useEffect(() => {
    if (walletAdapter.wallet) {
      walletAdapter
        .connect()
        .then(() => {
          // dispatch(updateSolanaSelectedWallet({ wallet: walletName }))
        })
        .catch((e) => {
          console.log(e)
          setWalletView(WALLET_VIEWS.OPTIONS)
        })
    }
  }, [walletAdapter, walletAdapter.wallet])

  // useEffect(() => {
  //   if (wallet) {
  //     connect().catch(() => {})
  //   }
  // }, [wallet, connect]

  // useEffect(() => {

  // }, [walletAdapter])

  const tryActivation = useCallback(
    async (walletName) => {
      try {
        // setPendingConnector(connector)
        setWalletView(WALLET_VIEWS.PENDING)
        // dispatch(updateConnectionError({ connectionType, error: undefined }))
        if (!walletAdapter.connected) {
          walletAdapter.select(walletName)
        }

        dispatch(updateSolanaSelectedWallet({ wallet: walletName }))
      } catch (error) {
        setWalletView(WALLET_VIEWS.OPTIONS)
        console.debug(`solana wallet connection error: ${error}`)
      }
    },
    [walletAdapter, dispatch],
  )

  const getModalContent = () => {
    if (walletView === WALLET_VIEWS.OPTIONS) {
      return <SolanaWalletOptions tryActivation={tryActivation} />
    }

    if (walletView === WALLET_VIEWS.PENDING) {
      return <PendingView goBack={() => setWalletView(WALLET_VIEWS.OPTIONS)} />
    }
  }
  return <div>{getModalContent()}</div>
}

export default SolanaWalletView
