import React from 'react'
import { Flex, Link, Text } from '@chakra-ui/react'
import LoadingText from '../../../components/Loader/LoadingText'
import { CheckCircleIcon, ExternalLinkIcon } from '@chakra-ui/icons'
import Loader from '../../../components/Loader'

const LoadingItem = ({ completed, title, link, hasLink, index }) => {
  return (
    <Flex alignItems="center" mb="10px" width={'70%'}>
      {!completed ? (
        <Loader size="md" mr={'10px'} />
      ) : (
        <CheckCircleIcon width={'24px'} height={'24px'} color="#01B652" mr={'10px'} />
      )}

      <Text>
        Transaction {index}: {title}
      </Text>
      {hasLink && (
        <Link href={link} isExternal>
          <ExternalLinkIcon ml={'10px'} cursor="pointer" />
        </Link>
      )}
    </Flex>
  )
}

const MintingLoader = ({ list = [], completed }) => {
  const _list = list.filter((l) => l.enable)
  return (
    <>
      <Text fontSize={24} fontWeight={600} width={'70%'} mb={4}>
        Minting {completed ? 'Completed' : 'In Progress'}
      </Text>
      {_list.map((props, index) => (
        <LoadingItem {...props} index={index + 1} />
      ))}
    </>
  )
}

export default MintingLoader
