import React, { useEffect, useState } from 'react'
import { Box, Flex, Text, Tooltip } from '@chakra-ui/react'
import styled from '@emotion/styled'
import { ChevronDownIcon, ChevronUpIcon, QuestionOutlineIcon } from '@chakra-ui/icons'
import BigNumber from 'bignumber.js'
import Account from '../components/Account'

const StyledCard = styled(Box)`
  //border: 1px solid #eaeaea;
  box-shadow: 0 5px 10px rgb(0, 0, 0, 12%);
  border-radius: 10px;
  padding: 10px;
  margin: 10px 0;
`

export const Item = ({ title, value, info }) => {
  return (
    <Flex justifyContent="space-between">
      <Text fontSize={12} fontWeight={600} opacity={'0.8 !important'}>
        {title}{' '}
        {info && (
          <Tooltip label={info}>
            <QuestionOutlineIcon />
          </Tooltip>
        )}
      </Text>
      <Text fontSize={12} fontWeight={600}>
        {value}
      </Text>
    </Flex>
  )
}

const SwapDetail = ({ quote, getQuote, toCurrency, solAmountIn }) => {
  const [collapse, setCollapse] = useState(false)
  const [toSolQuote, setToSolQuote] = useState({})
  const toggleCollapse = () => {
    setCollapse((state) => !state)
  }

  useEffect(() => {
    const fetchInfo = async () => {
      if (!!Object.keys(quote).length) {
        try {
          const baseAmount = new BigNumber(solAmountIn).dividedBy(quote.minAmountOut.toFixed())
          const _toSolQuote = await getQuote(baseAmount, true)
          setToSolQuote(_toSolQuote)
        } catch (e) {
          console.log(e)
        }
      }
    }

    fetchInfo()
  }, [quote])

  if (!Object.keys(toSolQuote).length) return null

  const { amountOut, currentPrice, executionPrice, fee, minAmountOut, priceImpact } = toSolQuote

  return (
    <StyledCard>
      <Flex justifyContent="space-between" alignItems="center">
        <Text fontWeight={500} fontSize={14}>
          1 {toCurrency} = {currentPrice?.toFixed()} SOL
        </Text>
        {collapse ? (
          <ChevronUpIcon cursor="pointer" boxSize={'24px'} onClick={toggleCollapse} />
        ) : (
          <ChevronDownIcon cursor="pointer" boxSize={'24px'} onClick={toggleCollapse} />
        )}
      </Flex>
      {!collapse && (
        <>
          <Item
            title="Minimum Received"
            value={`${minAmountOut.toFixed()} SOL`}
            info="The least amount of tokens you will receive on this trade"
          />
          <Item
            title="Price Impact"
            value={`${priceImpact.toFixed()}%`}
            info="The difference between the market price and estimated price due to trade size"
          />
          <Item
            title="Slippage Tolerance"
            value="5%"
            info="The maximum difference between your estimated price and execution price"
          />
          <Item title="Swap Fee" value={`${fee.toFixed()} ${toCurrency}`} info="" />

          <Item
            title="Payment wallet"
            value={<Account selectedWallet="Phantom" />}
            info="Payment will be made from this account."
          />
          <Item
            title="Receivable wallet"
            value={<Account selectedWallet="Phantom" />}
            info="NFT will be minted to this account and transferred to the same account"
          />
        </>
      )}
    </StyledCard>
  )
}

export default SwapDetail
