import React from 'react'
import {
  getHasCoinbaseExtensionInstalled,
  getHasMetaMaskExtensionInstalled,
  getIsInjected,
} from '../../connection/utils'
import useUserAgent from '../../utils/userAgent'
import { InjectedOption, InstallMetaMaskOption, MetaMaskOption } from '../../components/WalletModal/InjectedOption'
import { CoinbaseWalletOption, OpenCoinbaseWalletOption } from '../../components/WalletModal/CoinbaseWalletOption'
import { WalletConnectOption } from '../../components/WalletModal/WalletConnectOption'
import { Box, Text } from '@chakra-ui/react'

const WalletOptions = ({ tryActivation }) => {
  const isMobile = useUserAgent()
  function getOptions() {
    const isInjected = getIsInjected()
    const hasMetaMaskExtension = getHasMetaMaskExtensionInstalled()
    const hasCoinbaseExtension = getHasCoinbaseExtensionInstalled()

    const isCoinbaseWalletBrowser = isMobile && hasCoinbaseExtension
    const isMetaMaskBrowser = isMobile && hasMetaMaskExtension
    const isInjectedMobileBrowser = isCoinbaseWalletBrowser || isMetaMaskBrowser

    let injectedOption
    if (!isInjected) {
      if (!isMobile) {
        injectedOption = <InstallMetaMaskOption />
      }
    } else if (!hasCoinbaseExtension) {
      if (hasMetaMaskExtension) {
        injectedOption = <MetaMaskOption tryActivation={tryActivation} />
      } else {
        injectedOption = <InjectedOption tryActivation={tryActivation} />
      }
    }

    let coinbaseWalletOption
    if (isMobile && !isInjectedMobileBrowser) {
      coinbaseWalletOption = <OpenCoinbaseWalletOption />
    } else if (!isMobile || isCoinbaseWalletBrowser) {
      coinbaseWalletOption = <CoinbaseWalletOption tryActivation={tryActivation} />
    }

    const walletConnectionOption =
      (!isInjectedMobileBrowser && <WalletConnectOption tryActivation={tryActivation} />) ?? null

    return (
      <>
        {injectedOption}
        {coinbaseWalletOption}
        {walletConnectionOption}
      </>
    )
  }
  return (
    <Box bg="background">
      <Text color={'foreground'} fontSize={18} fontWeight={500}>
        Connect Wallet
      </Text>
      {getOptions()}
    </Box>
  )
}

export default WalletOptions
